@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.media-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 325px 325px 220px;
  gap: 15px;

  img:nth-child(2) {
    grid-column: auto / span 3;
    grid-row: auto / span 2;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 768px) {
  .media-container {
    grid-template-rows: 125px 125px 80px;
  }
}
