@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.container {
  display: flex;
  flex-direction: column;

  background: #f5f8ff;
  border-radius: 10px;

  padding: 20px 15px;

  h4 {
    font-size: calc(0.6rem + 1vw);
    font-weight: 600;
  }

  .info {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: baseline;

    h6 {
      font-size: 20px;
      font-weight: 600;

      margin-top: 40px;
    }

    p {
      font-size: 15px;

      font-weight: 500;
    }
  }
}
