@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  position: relative;

  button {
    display: inline-block;
    padding: 16px 50px;
    width: fit-content;
    background-color: v.$primary;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    border-radius: 5px;

    margin-top: 7px;

    transition: v.$transition;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    a {
      outline: none;
      text-decoration: none;
      color: wheat;
      font-weight: 600;
    }

    &:hover {
      background-color: v.$secondary;
      border: none;
      color: white;
    }
  }
}

@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    .search {
      width: 100%;
      margin-top: 25px;
      input {
        width: 100%;
        min-width: unset;
      }
    }
  }
}
