@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  .info {
    display: flex;
    flex-direction: column;
    justify-content: center;

    .info-header {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 0 8px;
      h2 {
        font-weight: 600;
        font-size: calc(1rem + 1vw);
      }

      .rate-stars {
        font-size: 30px;
      }
    }
    .info-body {
      display: flex;
      flex-direction: row;
      gap: 0 5px;

      margin-top: 10px;
      p {
        font-weight: 500;
        font-size: calc(0.5rem + 1vw);
      }
    }
  }
  .actions {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0 25px;

    .button {
      display: inline-block;
      padding: 12px 20px;
      width: fit-content;
      background-color: v.$primary;
      border: none;
      color: white;
      text-align: center;
      text-decoration: none;
      border-radius: 5px;

      margin-top: 7px;

      transition: v.$transition;

      font-weight: 600;
      font-size: 20px;

      &:hover {
        background-color: v.$secondary;
        border: none;
        color: white;
        transform: scale(0.9);
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;

    .info {
      .info-header {
        .rate-stars {
          font-size: 20px;
        }
      }
    }
    .actions {
      .button {
        padding: 10px 15px;

        font-size: 15px;
      }
      svg {
        transform: scale(0.7);
      }
    }
    .search {
      width: 100%;
      margin-top: 25px;
      input {
        width: 100%;
        min-width: unset;
      }
    }
  }
}
